export const validateUrl = (url) => {
    try {
        new URL(url);
        return true;
    } catch {
        return false;
    }
};

// Add MIME type to extension mapping
export const mimeTypeToExtension = {
    'image/jpeg': '.jpg',
    'image/png': '.png',
    'image/webp': '.webp',
    'image/svg+xml': '.svg'
};

// Update the placeholder image URL
export const getPlaceholderImage = () =>
    'data:image/svg+xml;base64,';
